import styled from '@emotion/styled';
import Container from '@mui/material/Container';

export default function Design(params) {
    return(
        <>
            <DesignWrap id='design'>
                <div className='dp-box'>
                    <h2>DESGIN</h2>
                    <div className='phoneWrap'>
                        <Phone>
                            <div className='phone1'>
                                <img src='/img/phone.png' alt=' ' width="100%"></img>
                            </div>
                            <div className='phoneImg'>
                                <img src='/img/phone1.PNG' alt=" " width="100%"></img>
                            </div>
                        </Phone>
                        <Phone>
                            <div className='phone2'>
                                <img src='/img/phone.png' alt=' ' width="100%"></img>
                            </div>
                            <div className='phoneImg2'>
                                <img src='/img/phone2.PNG' alt=" " width="100%"></img>
                            </div>
                        </Phone>
                        <Phone>
                            <div className='phone1'>
                                <img src='/img/phone.png' alt=' ' width="100%"></img>
                            </div>
                            <div className='phoneImg'>
                                <img src='/img/phone3.PNG' alt=" " width="100%"></img>
                            </div>
                        </Phone>
                    </div>
                </div>
            </DesignWrap>
        </>
    )
}

const DesignWrap = styled(Container)`
    width : 100%;
    height: 100vh;
    display:flex;
    align-items: center;
    justify-content : center;
    .dp-box{
        display:flex;
        flex-direction : column;
        align-items: center;
        justify-content : space-between;
        height : 100%;
    }
    h2{
        font-size: 40px;
        font-family: 'Josefin Sans', sans-serif;
        font-weight: bold;
        padding-top: 170px;
    }
    .phoneWrap{
        display:flex;
        align-items: baseline;
    }

    @media(max-width : 768px) {
        height : 100%;
    }
    `;
    
const Phone = styled.div`
    position : relative;
    margin : 0 10px;
    .phone1{
        width : 320px;
    }
    .phone2{
        width : 420px;
    }
    .phoneImg{
        width : 280px;
        height: 530px;
        overflow-y: scroll;
        border-top-left-radius: 30px;
        border-top-right-radius: 30px;
        position: absolute;
        top: 40px;
        left: 50%;
        transform: translate(-50%, 0);  
        -ms-overflow-style: none; 
         &::-webkit-scrollbar{ 
             display:none; 
        }
    }
    .phoneImg2{
        width : 370px;
        height: 700px;
        overflow-y: scroll;
        border-top-left-radius: 40px;
        border-top-right-radius: 40px;
        position: absolute;
        top: 50px;
        left: 50%;
        transform: translate(-50%, 0);  
        -ms-overflow-style: none; 
         &::-webkit-scrollbar{ 
             display:none; 
        }
    }

    @media(max-width : 768px) {
        .phone2{
            width : 320px;
        }
        .phoneImg2{
            width : 280px;
            height: 530px;
            top: 40px;
        }
    }

`;