import styled from '@emotion/styled';
import Container from '@mui/material/Container';
export default function Main1(params) {
    return(
        <>
            <BackgroundImg>
                <MainWrap>
                    <div className='title'>
                        <h1>
                           , <br/>
                        <strong> .</strong>
                        </h1>
                        <p>
                        The journey to a wider world, <br/>
                        get on board with Laputa
                        </p>
                    </div>
                    <a href='https://docs.google.com/forms/d/1a1ASz3NoaD7fcUlso08JRguj1KProTvVL3FIpMEoQ4M/edit'>
                    <div className='btnCircle'>
                        <div className='line'></div>
                        <div className='circle'>
                                <h2>CONTACT US</h2>
                                <p></p>
                        </div>
                    </div>
                    </a>
                </MainWrap>
            </BackgroundImg>
        </>
    )
}

const BackgroundImg = styled.div`
    width : 100%;
    height: 100vh;
    background-image : url(../img/main1.png);
    background-size : cover;
    background-position: center;

    display:flex;
    align-items : center;
    justify-content : center;

 
`;

const MainWrap = styled(Container)`
    display:flex;
    align-items : center;
    justify-content : space-between;
    width : 1200px;
    margin : 0 auto;
    .title{
        h1{
            font-size: 35px;
            color: #fff;
            line-height :1.5;
            font-weight: 300;
            margin-bottom : 30px;
            strong{
                font-size :45px;
                font-weight: 700;
            }
        }
    }
    p{
        color: #fff;
        font-size: 20px;
        line-height :1.5;
        font-weight: 300;

    }
    h2{
        color: #fff;
        font-size: 20px;
        font-weight: 500;
        text-align: center;
    }

    .btnCircle{
        display:flex;
        align-items : center;
    }
    .line{
        width : 260px;
        border : 1px solid #fff;
    }
    .circle{
        width : 320px;
        height: 320px;
        border : 2px solid #fff;
        border-radius : 50%;
        display:flex;
        align-items : center;
        justify-content : center;
        cursor : pointer;
        flex-direction : column;
        p{
            text-align: center;
            margin-top : 20px;
        }
    }
    .circle:hover{
        background-color : rgba(255,255,255,.6);
        transition : background-color .5s ease;
       p{
           color: #000;
       }
       h2{
            color: #000;
       }
    }


    @media(max-width : 1200px) {
        flex-direction : column;
        .line{
            display: none;
        }
        .circle{
            width : 230px;
            height : 230px;
            display: flex;
            align-items: center;
            flex-direction: column;
        }
        .title{
            margin-bottom : 40px;
            h1{
                text-align : center;
                font-size: 25px;
                strong{
                    font-size: 30px;
                }
            }
            p{
                text-align : center;
            }
        }
    }

`;