const PeopleWrap = styled(Container)`
    padding-bottom : 100px;
    h2{
        font-size: 40px;
        font-family: 'Josefin Sans', sans-serif;
        font-weight: bold;
        padding-top : 150px;
        margin-bottom : 10px;
    }
    p{
        font-size: 20px;
        line-height : 1.5;
    }
    .dp-flex{
        display:flex;
        align-items: center;
        justify-content : space-between;
    }
    .border{
        width : 800px;
        border: 1px solid #000;
    }
    .profileWrap{
        margin-top : 50px;
        display:flex;
        align-items: flex-start;
        flex-wrap : wrap;
    }
    h5{
        font-size: 20px;
        font-family: 'Josefin Sans', sans-serif;
        font-weight: bold;
        margin : 50px 0;
    }
    .mr-20{
        margin-right: 20px;
    }
    @media(max-width : 1200px) {
        .border{
            display:none;
        }
        .profileWrap{
            justify-content: center;
        }
        h2{
            font-size: 30px;
        }
    }
`;

const Profile = styled.div`
    width : 220px;
    display : flex;
    align-items: center;
    flex-direction: column;
    margin-right : 10px;
    .profileImg{
        width : 220px;
        height: 220px;
        margin-bottom : 20px;
    }
    h3{
        font-size: 20px;
        font-weight: bold;
        font-family: 'Josefin Sans', sans-serif;
        margin-bottom  : 10px;
    }
    small{
        font-size: 16px;
        line-height: 1.5;
        text-align : center;
    }
    h4{
        font-size: 16px;
        line-height: 1.5;
        font-weight: bold;
        margin-bottom : 20px;
        text-align : center;
    }
    p{
        font-size: 16px;
        line-height: 1.5;
        text-align : center;
    }
    .mb-20{margin-bottom : 20px;}
    @media(max-width : 1200px) {
        width : 180px;
        .profileImg{
            width : 180px;
            height: 180px;
        }
    }
`;