import styled from "styled-components"

export default function Value(params) {
    return(
        <>
            <Background id="val">
                <ValueWrap>
                    <h2>VALUE TO PURSUE</h2>
                    <div className="valueWrap">
                        <div className="Col">
                            <ValueBox className="mb-50">
                                <div className="circle-box">
                                    <div className="circle">
                                        <h3>01</h3>
                                    </div>
                                    <div className="border"></div>
                                </div>
                                <div className="text">
                                    <h4><span>experience</span></h4>
                                    <p>
                                        <br/>
                                       
                                    </p>
                                </div>
                            </ValueBox>
                            <ValueBox className="mb-50">
                                <div className="circle-box">
                                    <div className="circle">
                                        <h3>02</h3>
                                    </div>
                                    <div className="border"></div>
                                </div>
                                <div className="text">
                                    <h4><span>scale</span></h4>
                                    <p>
                                      5   <br/>
                                          <br/>
                                          
                                    </p>
                                </div>
                            </ValueBox>
                            <ValueBox>
                                <div className="circle-box">
                                    <div className="circle">
                                        <h3>03</h3>
                                    </div>
                                    <div className="border"></div>
                                </div>
                                <div className="text">
                                    <h4>ESG<span>Environment, Social, Governance</span></h4>
                                    <p>
                                          <br/>
                                      .
                                    </p>
                                </div>
                            </ValueBox>
                        </div>
                        <div className="Col">
                            <ValueBox className="mb-50">
                                <div className="circle-box">
                                    <div className="circle">
                                        <h3>04</h3>
                                    </div>
                                    <div className="border"></div>
                                </div>
                                <div className="text">
                                    <h4>  <span>service</span></h4>
                                    <p>
                                          <br/>
                                           <br/>
                                      .
                                    </p>
                                </div>
                            </ValueBox>
                            <ValueBox className="mb-50">
                                <div className="circle-box">
                                    <div className="circle">
                                        <h3>05</h3>
                                    </div>
                                    <div className="border"></div>
                                </div>
                                <div className="text">
                                    <h4><span>community</span></h4>
                                    <p>
                                          <br/>
                                    ,    ,  <br/>
                                         . <br/>
                                         <br/>
                                     
                                    </p>
                                </div>
                            </ValueBox>
                        </div>
                    </div>
                </ValueWrap>
            </Background>
        </>
    )
}

const Background = styled.div`
    width : 100%;
    height: 100vh;
    background-image : url(../img/main3.png);
    background-size : cover;
    background-position: center;

    display:flex;
    align-items: center;
    justify-content : center;

    @media(max-width : 1200px) {
        height : 100%;
        padding : 100px 0;
    }
`;

const ValueWrap = styled.div`
    .valueWrap{
        display:flex;
        align-items: flex-start;
        margin-top : 100px;
        flex-wrap: wrap;
    }
    .Col:first-of-type{
        margin-right: 50px;
    }
    h2{
        font-size: 40px;
        font-family: 'Josefin Sans', sans-serif;
        font-weight: bold;
        color : #fff;
        text-align : center;
    }
    .mb-50{
        margin-bottom : 50px;
    }    
    @media(max-width : 1200px) {
        .valueWrap{
            justify-content : center;
            padding : 0 20px;
        }
        .Col{
            width : 100%;
        }
        .Col:first-of-type{
            margin-right: 0;
            margin-bottom : 50px;
        }
        h2{
            font-size: 30px;
        }
    }
`;

const ValueBox = styled.div`
    display:flex;
    align-items: center;
     
    .circle{
        width : 100px;
        height : 100px;
        background-color : #fff;
        border-radius: 50%;
        display:flex;
        align-items: center;
        justify-content : center;
    }
    h3{
        font-size: 30px;
        font-family: 'Josefin Sans', sans-serif;
        font-weight: bold;
        text-align : center;
    }
    .border{
        width : 60px;
        border: 1px solid #fff;
    }
    .circle-box{
        display:flex;
        align-items: center;
        margin-right: 50px;
    }
    .text{
        h4{
            font-size : 25px;
            color: #fff;
            font-weight: bold;
            line-height: 1.4;
            margin-bottom : 20px;
            span{
                font-size : 16px;
                font-weight: 300;
                margin-left: 10px;
            }
        }
        p{
            font-size : 16px;
            font-weight: 300;
            line-height: 1.4;
            color: #fff;
        }
    }
    @media(max-width : 768px) {
        .border{
            display : none;
        }
    }
`;