import * as React from 'react';
import Box from '@mui/material/Box';
import SwipeableDrawer from '@mui/material/SwipeableDrawer';
import IconButton from '@mui/material/IconButton';
import List from '@mui/material/List';
import CloseIcon from '@mui/icons-material/Close';
import styled from '@emotion/styled';
import MenuIcon from '@mui/icons-material/Menu';
export default function MobileNav() {
  const [state, setState] = React.useState({
    top: false,
  });

  const toggleDrawer = (anchor, open) => (event) => {
    if (
      event &&
      event.type === 'keydown' &&
      (event.key === 'Tab' || event.key === 'Shift')
    ) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };

  const list = (anchor) => (
    <StyledBox
      sx={{ width: anchor === 'top' || anchor === 'bottom' ? 'auto' : 250 }}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
        <StyledIconButton onClose={toggleDrawer(anchor, false)}><CloseIcon/></StyledIconButton>
      <StyledList>
        <a href='/#design'>Design</a>
      </StyledList>
      <StyledList>
        <a href='/#bizPoint'>Business Point</a>
      </StyledList>
      <StyledList>
        <a href='/#val'>Value to Purse</a>
      </StyledList>
      <StyledList>
        <a href='/#people'>People</a>
      </StyledList>
      <StyledList>
        <a href='/contactUs'>Contact Us</a>
      </StyledList>
    </StyledBox>
  );

  return (
    <div>
      {['top'].map((anchor) => (
        <React.Fragment key={anchor}>
          <StyledIconButton onClick={toggleDrawer(anchor, true)}><MenuIcon/></StyledIconButton>
          <SwipeableDrawer
            anchor={anchor}
            open={state[anchor]}
            onClose={toggleDrawer(anchor, false)}
            onOpen={toggleDrawer(anchor, true)}
          >
            {list(anchor)}
          </SwipeableDrawer>
        </React.Fragment>
      ))}
    </div>
  );
}

const StyledBox = styled(Box)`
    padding: 20px;
    background-color: #000;
    a{
        color: #fff;
        font-size : 18px;
    }
`;

const StyledList = styled(List)`
    padding: 20px 0;
`;

const StyledIconButton = styled(IconButton)`
    padding: 0;
    svg{
        color: #fff;
    }
`;