import styled from '@emotion/styled';
import Container from '@mui/material/Container';
import MobileNav from './MobileNav';

export default function Nav(params) {
    return(
        <>
            <NavWrap>
                <ContainerBox>
                    <div className='logoBox'>
                        <a href='/'><img src='/img/logo.png' alt=' ' width="100%"></img></a>
                    </div>
                    <ul className='menu'>
                        <li>
                            <a href='/#design'>Design</a>
                        </li>
                        <li>
                            <a href='/#bizPoint'>Business Point</a>
                        </li>
                        <li>
                            <a href='/#val'>Value to Purse</a>
                        </li>
                        <li>
                            <a href='/#people'>People</a>
                        </li>
                        <li>
                            {/* <a href='/contactUs'>Contact Us</a> */}
                            <a href='https://docs.google.com/forms/d/1a1ASz3NoaD7fcUlso08JRguj1KProTvVL3FIpMEoQ4M/edit'>Contact Us</a>
                        </li>
                    </ul>
                    <div className='mobileMenu'>
                        <MobileNav/>
                    </div>
                </ContainerBox>
            </NavWrap>
        </>
    )
}

const NavWrap = styled.div`
    background-color : #000;
    padding: 20px 0;
    position: fixed;
    top : 0;
    z-index: 99;
    width : 100%;
    .logoBox{
        width  : 110px;
    }
    .menu{
        display : flex;
        align-items: center;
        li{
            margin-right: 50px;
            a{
                color: #fff;
                font-size : 18px;
            }
        }
        li:first-of-type{
            margin-left : 50px;
        }
    }
    .mobileMenu{
        display:none;
    }
    @media(max-width : 768px) {
        .menu{
            display: none;
        }
        .mobileMenu{
            display: block;
        }
        .logoBox{
            width : 100px;
        }
    }
`;

const ContainerBox = styled(Container)`
    display : flex;
    align-items: center;
    @media(max-width : 768px) {
        justify-content: space-between;
    }
`;