import styled from '@emotion/styled';

export default function BusinessPoint(params) {
    return(
        <>
            <Background id='bizPoint'>
                <BusinessWrap>
                    <h2>BUSINESS POINT</h2>
                    <div className='pointWrap'>
                        <PointBox>
                            <div className='circle mb-100'>
                                <div>
                                    <h3>01</h3>
                                    <h4>  </h4>
                                </div>
                                <div className='border'></div>
                            </div>
                            <p className='mb-20'>
                                , , , ,   <br/>
                                    
                            </p>
                            <p>
                                     
                            </p>
                        </PointBox>
                        <PointBox className='reverse-box'>
                            <p className='mb-20'>
                                Growth hacking 
                            </p>
                            <p>
                                SEO
                            </p>
                            <div className='circle mt-100'>
                                <div>
                                    <h3>02</h3>
                                    <h4></h4>
                                </div>
                                <div className='border2'></div>
                            </div>
                        </PointBox>
                        <PointBox>
                            <div className='circle mb-100'>
                                <div>
                                    <h3>03</h3>
                                    <h4>CDP</h4>
                                </div>
                                <div className='border'></div>
                            </div>
                            <p className='mb-20'>
                                 
                            </p>
                            <p>
                                 (CX)  
                            </p>
                        </PointBox>
                        <PointBox className='reverse-box'>
                            <p className='mb-20'>
                                 
                            </p>
                            <p className='mb-20'>
                                
                            </p>
                            <p>
                                
                            </p>
                            <div className='circle mt-100'>
                                <div>
                                    <h3>04</h3>
                                    <h4> <br/>
                                      </h4>
                                </div>
                                <div className='border2'></div>
                            </div>
                        </PointBox>
                    </div>
                </BusinessWrap>
            </Background>
        </>
    )
}

const Background = styled.div`
    width : 100%;
    height: 100vh;
    background-image : url(../img/main2.png);
    background-size : cover;
    background-position: center;

    display:flex;
    align-items: center;
    justify-content : center;

    @media(max-width : 1200px) {
        height : 100%;
        padding: 100px 0 50px 0;
    }
`;

const BusinessWrap = styled.div`
    display:flex;
    align-items: center;
    justify-content : center;
    flex-direction : column;
    h2{
        font-size: 40px;
        font-family: 'Josefin Sans', sans-serif;
        font-weight: bold;
        color : #fff;
    }
    .pointWrap{
        margin-top : 100px;
        display:flex;
        align-items: center;
        flex-wrap : wrap;
    }

    @media(max-width : 1200px) {
        .reverse-box{
            flex-direction : column-reverse;
        }
        h2{
            font-size: 30px;
        }
        .pointWrap{
            justify-content: center;
        }
    }
`;

const PointBox = styled.div`
    display:flex;
    align-items: center;
    justify-content : center;
    flex-direction : column;
    .mb-100{margin-bottom :100px;}
    .mt-100{margin-top : 100px;}
    .mb-20{margin-bottom: 20px;}
    .circle{
        width : 270px;
        height : 270px;
        border: 2px solid #fff;
        border-radius: 50%;
        display:flex;
        align-items: center;
        justify-content : center;
        position: relative;

        h3{
            font-size: 40px;
            color: #fff;
            font-family: 'Josefin Sans', sans-serif;
            font-weight: bold;
            text-align : center;
            margin-bottom : 20px;
        }
        h4{
            font-size : 20px;
            color: #fff;
            font-weight: bold;
            text-align : center;
            line-height: 1.4;
        }
    }
    p{
        font-size: 16px;
        line-height: 1.5;
        color: #fff;
        font-weight: 200;
        text-align : center;
    }
    .border{
        height: 127px;
        border: 1px solid #fff;
        position: absolute;
        top: 80%;
        left : 50%;
        trasform : translate(-50%, 0);
    }
    .border2{
        height: 127px;
        border: 1px solid #fff;
        position: absolute;
        top: -25%;
        left : 50%;
        trasform : translate(-50%, 0);
    }
    @media(max-width : 1200px) {
        margin : 20px;
        .border,.border2{
            display:none;
        }
        .mb-100{
            margin-bottom : 50px;
        }
        .mt-100{
            margin-top : 0px;
        }
        .circle{
            width : 230px;
            height: 230px;
            margin-bottom : 50px;
        }
        p{
            margin-bottom : 20px;
        }
     }
`;