import * as React from 'react';
import ReactDOM from 'react-dom';
import Nav from '../commponent/common/Nav';
import Main1 from '../commponent/main/Main1';
import Design from "../commponent/design/Design";
import BusinessPoint from '../commponent/businessPoint/BusinessPoint';
import Value from '../commponent/value/Value';
import styled from 'styled-components';
import People from '../commponent/people/People';

function Main(params) {
    return(
        <>
            <MainWrap>
                <Nav/>
                <Main1/>
                <Design/>
                <BusinessPoint/>
                <Value/>
                <People/>
            </MainWrap>
        </>
    )
}

const MainWrap = styled.div`
    @media(max-width : 1200px) {
        overflow-x: hidden;
    }
`;


export default Main;
